.page--shop-landing .entry-header {
	display: none;
}
.page--shop-landing.page #main .entry-content {
	max-width: 1140px;
    margin-bottom: 1rem;
    width: calc(100% - 2rem);
}
.page--shop-landing .entry-content {
	background-color: white;
	border: 1px solid currentColor;
	padding: 0 1rem 1rem !important;
}
@media (min-width: 1064px) {
	.page--shop-landing #content #main .entry-content {
		margin-top: calc(1rem + (100vw - 1064px) * 0.5);
		margin-bottom: calc(1rem + (100vw - 1064px) * 0.5);
	}
}
@media (min-width: 1140px) {
	.page--shop-landing #content #main .entry-content {
		margin-top: 60px;
		margin-bottom: 60px;
	}
}

.page--shop-landing .wp-block-columns {
	flex-direction: column;
}
.page--shop-landing #main .wp-block-column {
	position: relative;
	padding: 1rem;
	margin: 1rem 0 0;
}
.page--shop-landing #main .wp-block-column:first-child {
	margin-left: 0 !important;
}
.page--shop-landing #main .wp-block-column:last-child {
	margin-right: 0 !important;
}
@media (min-width: 720px) {
	.page--shop-landing .entry-content {
		padding: 0 2rem 2rem !important;
	}
	.page--shop-landing .wp-block-columns {
		flex-direction: row;
		padding-bottom: 0;
	}
	.page--shop-landing #main .wp-block-column {
		margin: 2rem 1rem 0;
		flex-basis: calc(50% - 1rem);
		padding: 1.8rem;
	}
}

.page--shop-landing .wp-block-column::before {
	border-width: 1px;
}
.page--shop-landing .wp-block-column::after {
	border-width: 3px;
}

.page--shop-landing .wp-block-column h2::before,
.page--shop-landing .wp-block-column h2::after {
	display: none !important;
}
.page--shop-landing .wp-block-column p {
	font-size: 0.825em;
	line-height: 1.5;
}
.page--shop-landing .wp-block-column .wp-block-handpicked-products {
	text-align: center;
	white-space: nowrap;
}
.page--shop-landing .wp-block-column .wc-block-grid__product-image {
	display: none;
}
.page--shop-landing .wp-block-column .wp-block-button,
.page--shop-landing .wp-block-column .wc-block-grid__products {
	font-size: 0.75em;
}
.page--shop-landing .wp-block-column .wp-block-button {
	justify-content: center;
}
.page--shop-landing .wp-block-column .wc-block-grid__products {
	flex-direction: column;
	align-items: center;
	flex-wrap: nowrap;
    max-width: 390px;
    margin: 0 auto;
}
@media (min-width: 1120px) {
	.page--shop-landing .wp-block-column .wc-block-grid__products {
		flex-direction: row;
		justify-content: space-between;
	}
}
.page--shop-landing .wp-block-column .wc-block-grid__product {
	max-width: none;
	text-transform: uppercase;
	margin: 0.7em 0 0;
	padding: 0;
	flex-grow: 0;
	flex-basis: auto;
}
.page--shop-landing .wp-block-column .wc-block-grid__product:first-child {
	margin-left: 0;
}
.page--shop-landing .wp-block-column .wc-block-grid__product:last-child {
	margin-right: 0;
}
.page--shop-landing .wp-block-column .wc-block-grid__product .btn {
	padding: 0.6em 0.9em 0.5em;
}
.page--shop-landing .wp-block-column .wc-block-grid__product-title {
	font: inherit;
	margin: 0;
}
.page--shop-landing #content .wp-block-button .wp-block-button__link {
	text-transform: uppercase;
	padding: 0.6em 1.3em 0.5em;
	font-size: 0.9em;
	margin: 0 1em 1.6em;
}
